body {
    font-family: $font;
}

h2 {
    font-size: $font-xl;
    text-align: center;
    font-weight: $font-bold;
}

h3 {
    font-size: $font-l;
    text-align: center;
    font-weight: $font-bold;
}

h4 {
    font-size: $font-xl;
    text-align: center;
    font-weight: $font-bold;
}

p {
    font-size: $font-xs;
    font-weight: $font-normal;
    padding: 0.5rem 0;
}

ul {
    list-style: none;
    padding: 0;
}

.principal {
    text-align: center;
    .principal__imgFondoMercado {
        height: 75vh;
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center;
        background-attachment: fixed;
        background-image: url("../images/Fondo2.jpg");
    }
    .principal__titulo {
        background: $color-titulo;
        background: $backg-titulo;
        .principal__titulo--primero {
            color: $color-titulo;
            font-size: $font-xxl;
        }
        .principal__titulo--segundo {
            color: white;
            font-size: $font-xl;
            text-align: center;
        }
    }
}

.carrito {
    position: fixed;
    width: 50px;
    height: 50px;
    z-index: 1600;
    right: 30px;
    bottom: 70px;
    .carrito-img {
        width: 100%;
    }
}

.clearall {
    clear: both;
}

.filtros {
    display: flex;
    flex-direction: column;
}

#catalogo {
    .productos {
        .producto-box {
            width: $ancho-ppal/3;
            float: left;
            box-sizing: border-box;
            margin-bottom: 20px;
            img {
                width: 100%;
            }
        }
    }
}

#footer {
    a {
        color: white;
        text-decoration: none;
        font-size: $font-xs;
        font-weight: $font-light;
    }
    .logo img {
        width: $logo-normal*2/3;
    }
    .redes img {
        width: $redes-normal*4/5;
    }
}

@media all and (min-width: 576px) {
    p {
        font-size: $font-s;
    }
    li {
        list-style-type: none;
    }
    #footer {
        a {
            font-size: $font-xs;
        }
        .logo img {
            width: $logo-normal;
        }
        .redes img {
            width: $redes-normal;
        }
    }
}

